@import './variables.scss';

.RegistrationBody,
form {
  position: relative;
  margin: 0 auto;
}

.RegistrationBody {
  height: auto;
  width: 75%;
  b {
    font-size: 3rem;
    margin: 0 auto;
  }
  top: 5rem;
}

form {
  font-family: $fontFamilyBasic;
  top: 2rem;
  left: 20rem;

  .formName,
  .formEmail,
  .formOrganization,
  .formPassword {
    display: block;
    position: absolute;
    label {
      display: block;
    }
    input {
      top: 3rem;
      display: block;
      position: absolute;
    }
  }
  .formEmail {
    top: 8rem;
  }
  .formOrganization {
    top: 16rem;
  }
  .formPassword {
    top: 24rem;
  }
}

.registrationText,
.buttonRegister {
  font-family: $fontFamilyBasic;
  font-size: 1.5rem;
  height: 3rem;
  padding-left: 1rem;
  width: 40rem;
  border-radius: 3rem;
  border-width: calc(0.1rem + 0.1vmin);
  background-color: white;
}

.buttonRegister {
  font-size: 2rem;
  display: block;
  position: absolute;
  top: 36rem;
  left: 0rem;
  width: 50%;
  height: 4rem;
  margin: auto;
  background-color: black;
  color: white;
  box-shadow: none;
  transition: 0.5s;
}

button:hover {
  cursor: pointer;
  background-color: #ccc;
  border-color: #ccc;
  transition: 0.5s;
}
